@import url(https://fonts.googleapis.com/css2?family=Alata&family=Montserrat:wght@400;600&family=Roboto&family=Roboto+Mono&display=swap&family=Raleway:wght@300&family=Roboto+Mono&family=Courgette&family=Work+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
	--text-primary-color: #F1FAEE;
	--text-secondary-color: #A8DADC;
	--text-dark-color: #1D3557;
	--text-secondary-accent: #6D0B00;
	--accent-color: #E63946;
	--bg-1-color: #1D3557;
	--bg-2-color: #457B9D;
	--bg-3-color: #14263f;

	--bg-transparent-color: rgba(29, 53, 87, 0.6);

	/* --text-primary-color: #FAF0BC;
	--text-secondary-color: #C94748;
	--accent-color: #FDD177;
	--bg-1-color: #1B3B45;
	--bg-2-color: #1B3B45; */
	font-family: 'Raleway', sans-serif;
}

h1 {
	color: #E63946;
	color: var(--accent-color);
	font-family: 'Raleway', sans-serif;
}

h2, h3, h4, h5 {
	font-family: 'Raleway', sans-serif;
	color: #A8DADC;
	color: var(--text-secondary-color);
}

h4, h5 {
	font-family: 'Work Sans', sans-serif;
}

p {
	color: #F1FAEE;
	color: var(--text-primary-color);
	font-family: 'Raleway', sans-serif;
}

.background {
	background-color: #14263f;
	background-color: var(--bg-3-color);
}
@media (min-width: 1980px) {
	#landing--container video {
		width: 100vw!important;
	}
}

@media (min-width: 576px) {
	#header-content {
		margin-right: 0!important;
		margin-left: 12vw!important;
		max-width: -webkit-fit-content!important;
		max-width: -moz-fit-content!important;
		max-width: fit-content!important;
		text-align: left!important;
	}

	#landing--container video {
		left: 0!important;
		top: 0!important;
	}

	#social-buttons {
		font-size: 3rem!important;
	}
}

#landing--container {
	display: flex;
	height: 100vh;
	background-color: rgba(69, 123, 157, 0.5);
}

#landing--container video {
	position: fixed;
	z-index: -1;
	left: -55%;
	width: 2000px;
}

#landing--container h1 {
	font-size: 4rem!important;
	font-weight: 200;

}

#landing--container h2 {
	color: var(--text-primary-color);
}

#header-content {
	text-align: center;
	
	margin: 20vh auto;
	max-width: 95vw;
	min-width: 1vw;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}

#social-buttons {
	position: fixed;
    color: white;
    bottom: 0;
    left: 0;
	font-size: 2.5rem;
	z-index: 9999;
}

#social-buttons * {
 transition: all .1s ease-in;
	margin: 5px;
}

#social-buttons *:hover {
	cursor: pointer;
	color: var(--accent-color);
}

@media (min-width: 768px) { 
	.job-details h1{
		width: 80%;
		margin-top: 10px;
	}

	.content {
		margin-top: 0!important;
	}

	.job--thumbnail img {
		width: 100%!important;
	}
}

hr {
	border-color: var(--accent-color);
}

.content {
	margin-top: 30px;
}

.job-details h1 {
	text-align: center;
}

.job-details h2 {
	color: var(--text-primary-color);
}

.job-details h3 {
	color: var(--text-primary-color);
}

.job-details h5 {
	color: var(--text-secondary-color);
}

.job--thumbnail {
	text-align: center;
}

.job--thumbnail img {
	width: 50%;
    border-radius: 25px;
}
#skills-page {
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}
@media (min-width: 576px) { 
	#skills-chart  {
		font-size: 30px!important;
	}
}


@media (min-width: 992px) { 
	#skills-chart  {
		max-width: 80%!important;
		font-size: 30px!important;
	}

	.skill-container {
		padding-right: 30%;
	}
}

#skills-chart {
	white-space: nowrap;
	font-size: 6vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 30px auto;
    color: var(--text-secondary-color);
    font-family: 'Raleway', sans-serif;
	border-color: var(--bg-2-color)!important;
	background-color: var(--bg-transparent-color);
}

h2 {
	margin: 15px;
	color: var(--text-primary-color);;
}

.skill-container span{
 transition: all .1s ease-in;
}

.skill-container span:hover {
	transform: scale(1.1);
}

.meter-bar {
	width: 20%;
	height: 30%;
	background-color: var(--accent-color);
}

.border-left, .border-right {
	border-color: var(--text-secondary-color)!important;
}
@media (min-width: 768px) { 
}

hr {

}

#about-me p {
	color: var(--text-primary-color);
}

#about-me  img
{
	margin: auto;
	max-width: 100%;
    border-radius: 50%;
    overflow: hidden;
    width: 200px;
    height: 200px;
    object-fit: cover;
    object-position: 40% -6px;
}
#site--content .col .section {
	background-color: var(--bg-1-color);
}

#site--content .col .light-section {
	background-color: var(--bg-2-color);
}

#site--content .light-section h1 {
	color: var(--accent-color);
}

#site--content h1{
	font-size: 4rem;
	text-align: center;
	font-weight: 300;
}

#site--content .section {
	background-color: var(--bg-2-color);
	background-size: cover;
}

#site--content .my-skills {
	background-image: url(/personal-website-react/static/media/evening-55067.d03f4559.png);
}
#loading--spinner {
	
	position: fixed;

	width: 100vw;
	height: 100vh;

	top: 0;
	left: 0;

	z-index: 99999;
}

.side {
	background-color: var(--bg-1-color);
	position: absolute;
	
	width: 50vw;
	height: 100vh;

	
}

.right {
	left: 50%;
}

.l-animate {
	-webkit-animation: slideleft 0.5s ease-in-out .25s 1 normal forwards;
	        animation: slideleft 0.5s ease-in-out .25s 1 normal forwards;
}

@-webkit-keyframes slideleft {
  0% {
    left: 0%;
  }
  100% {
    left: -100%;
  }
}

@keyframes slideleft {
  0% {
    left: 0%;
  }
  100% {
    left: -100%;
  }
}

.r-animate {
	-webkit-animation: slideright 0.5s ease-in-out .25s 1 normal forwards;
	        animation: slideright 0.5s ease-in-out .25s 1 normal forwards;
}

@-webkit-keyframes slideright {
  0% {
    left: 50%;
  }
  100% {
    left: 100%;
  }
}

@keyframes slideright {
  0% {
    left: 50%;
  }
  100% {
    left: 100%;
  }
}

